<template>
  <div class="contanier">
    <div class="row" style="justify-content: center;" v-if="loading">
      <div class="flex-center spinner-box">
        <SpringSpinner
          :animation-duration="1500"
          :color="this.$themes.primary"
          :size="80"
        >
        </SpringSpinner>
      </div>
    </div>
    <div class="row" v-if="showInv" >
      <invoicesRBuy-Table :bill="bill" class="flex md12 sm12 xs12"/>
    </div>
    <div class="row" v-if="!showInv && !loading" style="justify-content: center;" >
      <va-card :title="$t('tables.labelinvoicesRBuy')" class="flex md8 sm12 xs12">
        <form @submit.prevent="okCreatefun()">
          <div class="row flex-center">
            <div class="flex md4 sm12 xs12 px-1">
              <label class="input-label"> {{$t('placeholder.invid_Bill')}} </label>
              <va-input v-model="invid_Bill"
                :placeholder="$t('placeholder.invid_Bill')" @keyup.enter="okCreatefun" required/>
            </div>
          </div>
          <div class="d-flex justify--center mt-3">
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.ok') }} </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import invoicesRBuyTable from './invoicesRBuy/invoicesRBuyTable'
import BillService from '../../../services/BIll/BillService'
import * as spinners from 'epic-spinners'

export default {
  name: 'invoicesRBuy',
  components: {
    invoicesRBuyTable,
    ...spinners,
  },
  data () {
    return {
      showInv: false,
      stopBtn: false,
      loading: false,
      bill: '',
      invid_Bill: '',
    }
  },
  methods: {
    okCreatefun () {
      this.stopBtn = true
      this.loading = true
      if (this.invid_Bill !== null && this.invid_Bill !== undefined && this.invid_Bill !== '') {
        const prams = {
          Bill_Id: this.invid_Bill,
          inv_type: 2,
        }
        BillService.getSearchBill(prams).then(
          response => {
            if (response.data[0].inv_Subs.length === 0) {
              this.loading = false
              this.stopBtn = false
              return this.showToast(this.$t('generic.thisBillIsEmpty'), {
                icon: 'fa-exclamation',
                position: 'bottom-center',
                duration: 2500,
                type: 'error',
              })
            }
            this.loading = false
            this.stopBtn = false
            this.bill = response.data[0]
            this.showInv = !this.showInv
          },
          err => {
            console.error(err)
            this.loading = false
            this.stopBtn = false
            this.showToast(this.$t('generic.error'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          },
        )
      } else {
        this.stopBtn = false
        this.loading = false
        this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
  },
}

</script>

<style lang="scss">
  .row-equal .flex {
    .va-card {
      height: 100%;
    }
  }

  .dashboard {
    .va-card {
      margin-bottom: 0 !important;
    }
  }

</style>
