<template>
  <va-card :title="$t('tables.labelinvoicesRSelllist') + bill.inv_id">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      :disabled="stopBtn"
      @click="showCreateModal()">
      {{ $t('buttons.createRINv') }}
    </va-button>
    <va-data-table
      :fields="fields"
      :data="bill.inv_Subs"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="products" slot-scope="props">
        {{props.rowData.products === null  ? '----': props.rowData.products.name }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="danger" @click="showDeleteModal(props.rowIndex)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="createModal" no-outside-dismiss
      :title=" $t('generic.invrsell') "
      :okText=" $t('modal.return') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.invrsellMsg') "
      @ok="okCreatefun()"
      @cancel="cancleCreatefun()">
    </va-modal>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="okDeletefun()">
        <div class="row flex-center">
          <div class="flex md12 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.quantity')}} </label>
            <va-input v-model="currentobject.quantity"
              type="number" min="0" :max="maxNum"
              :placeholder="$t('placeholder.quantity')" required/>
          </div>

        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="cancleDeletefun()">
            {{ $t('modal.cancel') }}
          </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>

  </va-card>

</template>

<script>
import BillService from '../../../../services/BIll/BillService'
export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      deleteModal: false,
      createModal: false,
      currentindex: Number,
      opjectBeforeEdit: '',
      currentobject: {},
      maxNum: 0,
      ApiRequest: {
        inv_Subs: [],
      },
    }
  },
  props: {
    bill: {},
  },
  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: '__slot:products',
        title: this.$t('tables.headings.name'),
      },
      {
        name: 'quantity',
        title: this.$t('tables.headings.quantity'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  methods: {

    /* cud api */
    create () {
      this.ApiRequest.total_Price = this.bill.total_Price
      this.ApiRequest.is_Cash = this.bill.is_Cash
      this.ApiRequest.is_Deliverd = this.bill.is_Deliverd
      this.ApiRequest.inv_dueDate = this.bill.inv_dueDate
      this.ApiRequest.date = this.bill.date
      this.ApiRequest.inv_ReturnId = this.bill.inv_id
      this.ApiRequest.paid = this.bill.paid
      this.ApiRequest.tax = this.bill.tax
      this.ApiRequest.iS_Paid = this.bill.iS_Paid
      this.ApiRequest.iS_Deleted = this.bill.iS_Deleted
      this.ApiRequest.notes = this.bill.notes
      this.ApiRequest.disscountPercentage = this.bill.disscountPercentage
      this.ApiRequest.liveStatus = this.bill.liveStatus
      this.ApiRequest.fK_Branchs = this.bill.branchs.id
      this.ApiRequest.fK_Customers = this.bill.customers.id
      this.ApiRequest.fK_Inv_type = 4
      this.ApiRequest.fK_Safes = this.bill.safes.id
      this.ApiRequest.fK_Inv_PayType = this.bill.inv_PayType.id
      this.ApiRequest.fK_Employees = this.bill.employees.id
      this.ApiRequest.fK_Pay_Type = this.bill.pay_Type.id
      this.ApiRequest.fK_Pay_Type = this.bill.pay_Type.id
      this.bill.inv_Subs.forEach(element => {
        var newSubs = {
          quantity: element.quantity,
          price: element.price,
          tax: element.tax,
          disscountPercentage: element.disscountPercentage,
          fK_Products: element.products.id,
          fK_Stock: element.stock.id,
          fK_Units: element.units.id,
        }
        this.ApiRequest.inv_Subs.push(newSubs)
      })
      BillService.create(this.ApiRequest).then(response => {
        if (response.headers['x-status'] === 'true') {
          location.reload()
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModal (index) {
      this.modelTitle = 'generic.returnThisItem'
      this.modelOktext = 'modal.confirm'
      this.deleteModal = true
      this.opjectBeforeEdit = Object.assign({}, this.bill.inv_Subs[index])
      this.currentobject = this.bill.inv_Subs[index]
      this.maxNum = this.currentobject.quantity
      this.currentindex = index
    },
    okDeletefun () {
      if (this.currentobject.quantity === this.maxNum) {
        this.bill.inv_Subs.splice(this.currentindex, 1)
      }
      this.deleteModal = false
    },
    cancleDeletefun () {
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
      this.deleteModal = false
    },
    /* end Delete model */

    /* create model */
    showCreateModal () {
      this.createModal = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
    },
    /* end create model */
  },
}

</script>

<style lang="scss" >
  i.fa.fa-exclamation {
    color: red !important;
  }

  .va-modal__inner {
    max-width: 100%;
  }

</style>
