import http from '../../http-common'

class BillService {
  getNewBill (customerId, companyActivityId) {
    return http.get(`Bill/GetProductsByCustomer?id=${customerId}&Company_Activity=${companyActivityId}`)
  }

  getUintAndPriceByCUstomer (productId, customerId) {
    return http.get(`Bill/GetProductUintAndPrice?productId=${productId}&customerId=${customerId}`)
  }

  getSearchBill (params) {
    return http.get('GetByInv_billOrDate', { params })
  }

  search (params) {
    return http.get('Bill/Search', { params })
  }

  getAll () {
    return http.get('Bill')
  }

  getAllPagnation (params) {
    return http.get('Bill/GetAllPaging', { params })
  }

  getAllForDelivery (params) {
    return http.get('Bill/GetAllForDelivery', { params })
  }

  get (id) {
    return http.get(`Bill/getById?Id=${id}`)
  }

  create (data) {
    return http.post('Bill', data)
  }

  update (id, data) {
    return http.put(`Bill?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Bill?Id=${id}`)
  }
}

export default new BillService()
